header {
    width: 100%;
    height: 60px;
    z-index: 10;
    position: relative;
}

.headerBox {
    position: fixed;
    z-index: 15;
    width: 100%;
    height: 60px;
    background-color: aqua;
    top: 0px;
}

.btnHome {
    position: absolute;
    height: 45px;
    top:50%;
    transform: translateY(-50%);
    right:4%;
    z-index: 100;
}




.headerWrap {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 50px;
    transition: 0.3s ease;
    background-color: #fff;
    box-shadow: 0px 5px 3px rgba(0,0,0, 0.1);
    font-size: 40px;
    font-weight: bold;
    letter-spacing: -3px;
}

.logo img {
    width: 100%;
}
.logo {
    width: 130px;
    position: relative;
    top: 50%;
    left: 23%;
    transform: translate(-50%,-47%);
}
.headerWrap.hide {
    transform: translateY(-60px);
}

.headerSlide {
    font-size: 20px;
    font-weight: bold;
    touch-action: none;
    position: fixed;
    padding-top: 50px;
    right: 0;
    z-index: 1;
    width: 58%;
    height: 100%;
    background-color: #3e55ff;
}

.headerSlide a {
    color: #fff;

}
.headerSlide.active {
    right: 0%;
    transition: right 0.4s;
}

.headerSlide.hide {
    right: -58%;
    transition: right 0.4s;
}

.headerSlide ul{
    padding: 50px 0 0 20px;
}
.headerSlide ul li {
    height: 50px;
    line-height: 50px;
}

.headerSubSlide {
    touch-action: none;
    width: 58%;
    height: 60px;
    background-color: #3e55ff;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 10;

}

.headerSubSlide.active {
    right: 0%;
    transition: right 0.4s;
}
.headerSubSlide.hide{
    right: -58%;
    transition: right 0.4s;
}

.viewEvent {
    font-size: 17px;
    padding: 8px;
    border-radius: 5px;
    position: fixed;
    z-index:100;
    background-color: #143ce2;
    color: white;
    bottom: 10px;
    right: 10px;
}

.closeBtn {
    color: #fff;
}
