/* css Reset */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* css Reset */

a {
	text-decoration: none;
	color: #333;
}


/* https://velog.io/@dosilv/React-mountunmount-%EC%8B%9C-%EC%95%A0%EB%8B%88%EB%A9%94%EC%9D%B4%EC%85%98-%EC%A3%BC%EA%B8%B0 */
@keyframes smoothAppear {
    from {
      opacity: 0;
      transform: translateY(15%);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }


.evtForm_box {
    width: 90%;
    height: 200px;
    position: relative;
    margin: 30px auto;
    animation: smoothAppear 0.8s;
}
.evtForm_img {
    width: 100%;
    height: 170px;
	border-radius: 20px;
	box-shadow: 4px 4px 5px #bbb;
	overflow: hidden;

}
.evtForm_img img {
    width: 100%;
    height: 100%;
}
.evtForm_date {
    position: absolute;
    bottom: 40px;
    left: 20px;
    background-color: white;
    padding: 4px 10px;
    border-radius: 5px;
    font-size: 13px;
    font-weight: bold;
}

.evtForm_title {
    font-size: 15px;
    font-weight: bold;
    padding-top: 10px;
    padding-left: 10px;
}

.evtForm_views {
    position: absolute;
    bottom: 40px;
    right: 20px;
    background-color: white;
    padding: 4px 10px 4px 26px;
    border-radius: 5px;
    font-size: 13px;
    font-weight: normal;
}
.evtForm_views img {
    width: 17px;
    position: absolute;
    left: 5px;
    bottom: 1px;
}

.react-icons {
    color: red;
}



.comm_box {
    width: 90%;
    margin: 0 auto;
    font-size: 14px;
}
.comm_box li {
    list-style: none;
    padding-bottom: 14px;
    padding-top: 14px;
    border-bottom: 1px solid#bbb;
}

.comm_box p {
    margin-top: 10px;
}

.comm_area {
    width: 100%;
    min-height: 80px;
    resize: none;
    box-sizing: border-box;
    margin-top: 20px;
}
.comm_box input {
    width: 100%;
    box-sizing: border-box;
}

.submitBtn {
    background-color: #3e55ff;
    border: none;
    height: 30px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}


.loadingText {
    font-size: 100px;
}

